// lang-switcher.js
// –ü–æ–ª–Ω—ã–π, –∞–∫–∫—É—Ä–∞—Ç–Ω—ã–π —Å–∫—Ä–∏–ø—Ç –¥–ª—è –ø–µ—Ä–µ–∫–ª—é—á–∞—Ç–µ–ª—è —è–∑—ã–∫–∞:
// - –û—Ç–∫—Ä—ã—Ç–∏–µ/–∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É, –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –≤–Ω–µ –∏ –ø–æ Esc
// - –í—ã–±–æ—Ä —è–∑—ã–∫–∞ –ø–æ –∫–ª–∏–∫—É –ù–ê –ë–û–ö–° –∏ –Ω–∞ —Å–∞–º —Ñ–ª–∞–≥
// - –ó–∞–ø–æ–º–∏–Ω–∞–Ω–∏–µ –≤ localStorage
// - –ü–µ—Ä–≤–∏—á–Ω–∞—è –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø–æ saved lang –∏–ª–∏ navigator.language
// - –ü–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏–µ –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Ñ–ª–∞–≥–∞ –≤ –Ω–∞—á–∞–ª–æ —Å–ø–∏—Å–∫–∞
// - –î–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å: tabindex/aria-* + —É–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Å –∫–ª–∞–≤–∏–∞—Ç—É—Ä—ã

import { applyTranslations } from './i18n';

export const initLangSwitcher = () => {
  const wrapper  = document.querySelector('.header-box-options-lang');
  const dropdown = wrapper?.querySelector('.header-box-options-lang-dropdown');
  if (!wrapper || !dropdown) return;

  const OPEN_CLASS = 'dropdownOpen';

  // a11y –Ω–∞ ¬´–∫–Ω–æ–ø–∫–µ¬ª
  dropdown.setAttribute('tabindex', '0');
  dropdown.setAttribute('role', 'button');
  dropdown.setAttribute('aria-haspopup', 'listbox');
  dropdown.setAttribute('aria-expanded', 'false');

  const isOpen = () => dropdown.classList.contains(OPEN_CLASS);
  const open   = () => { dropdown.classList.add(OPEN_CLASS);  dropdown.setAttribute('aria-expanded', 'true'); };
  const close  = () => { dropdown.classList.remove(OPEN_CLASS); dropdown.setAttribute('aria-expanded', 'false'); };

  const setLanguage = (lang) => {
    if (!lang) return;
    applyTranslations(lang);
    localStorage.setItem('lang', lang);

    // –ü–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π —Ñ–ª–∞–≥ –Ω–∞–≤–µ—Ä—Ö
    const img   = dropdown.querySelector(`img[data-lang-btn="${lang}"]`);
    const box   = img?.closest('.header-box-options-lang-dropdown-box');
    const first = dropdown.firstElementChild;
    if (box && first && box !== first) dropdown.insertBefore(box, first);
  };

  // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —è–∑—ã–∫–∞
  (() => {
    const saved = localStorage.getItem('lang');
    if (saved) {
      setLanguage(saved);
      return;
    }
    const map = {
      pl: 'pl', 'pl-PL': 'pl',
      en: 'en', 'en-GB': 'en', 'en-US': 'en',
      de: 'de', 'de-DE': 'de',
      ru: 'ru', 'ru-RU': 'ru'
    };
    const guess = map[navigator.language] || 'en';
    setLanguage(guess);
  })();

  // –ö–ª–∏–∫ –ø–æ –≤–∏–¥–∂–µ—Ç—É:
  // 1) –µ—Å–ª–∏ –∑–∞–∫—Ä—ã—Ç ‚Äî –≤—Å–µ–≥–¥–∞ –û–¢–ö–†–´–í–ê–ï–ú, –Ω–µ –ø—ã—Ç–∞–µ–º—Å—è –≤—ã–±—Ä–∞—Ç—å —è–∑—ã–∫
  // 2) –µ—Å–ª–∏ –æ—Ç–∫—Ä—ã—Ç ‚Äî –≤—ã–±–∏—Ä–∞–µ–º —è–∑—ã–∫ –ø–æ –∫–ª–∏–∫—É –Ω–∞ –±–æ–∫—Å/—Ñ–ª–∞–≥
  dropdown.addEventListener('click', (e) => {
    if (!isOpen()) { open(); return; }

    const box = e.target.closest('.header-box-options-lang-dropdown-box');
    if (!box) return;

    const img = box.querySelector('img[data-lang-btn]');
    if (!img) return;

    const lang = img.getAttribute('data-lang-btn');
    setLanguage(lang);
    close();
  });

  // –ö–ª–∞–≤–∏–∞—Ç—É—Ä–∞: Enter/Space ‚Äî toggle; Esc ‚Äî close
  dropdown.addEventListener('keydown', (e) => {
    if ((e.key === 'Enter' || e.key === ' ') && !e.repeat) {
      e.preventDefault();
      isOpen() ? close() : open();
    } else if (e.key === 'Escape') {
      close();
      dropdown.blur();
    }
  });

  // –ö–ª–∏–∫ –≤–Ω–µ ‚Äî –∑–∞–∫—Ä—ã–≤–∞–µ–º
  const onDocClick = (e) => {
    if (!wrapper.contains(e.target)) close();
  };
  document.addEventListener('click', onDocClick);

  // –ù–∞ —Å–ª—É—á–∞–π ¬´—É—Ç–µ—á–µ–∫¬ª –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —É–∑–ª–∞ (SPA/–ø–µ—Ä–µ—Ä–∏—Å–æ–≤–∫–∏)
  const cleanup = () => document.removeEventListener('click', onDocClick);
  // –ï—Å–ª–∏ –Ω—É–∂–Ω–æ ‚Äî —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–π cleanup –Ω–∞—Ä—É–∂—É
  // return cleanup;
};

// –ê–≤—Ç–æ-–∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è, –µ—Å–ª–∏ –º–æ–¥—É–ª—å –ø–æ–¥–∫–ª—é—á—ë–Ω –Ω–∞–ø—Ä—è–º—É—é
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', initLangSwitcher, { once: true });
} else {
  initLangSwitcher();
}