export function initLogoScroll() {
  const bigLogo = document.querySelector('.header-logo-img');
  const smallLogo = document.querySelector('.header-box-logo-img');
  if (!bigLogo || !smallLogo) return;

  // –ù–∞ –∫–∞–∫–æ–º —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–∏ (px) –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –ø–æ–ª–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥ —Å –±–æ–ª—å—à–æ–≥–æ –Ω–∞ –º–∞–ª—ã–π
  const fadePoint = 100;

  const clamp = (v, min, max) => Math.max(min, Math.min(max, v));

  function apply() {
    const y = window.scrollY || document.documentElement.scrollTop || 0;
    const ratio = clamp(y / fadePoint, 0, 1);

    // –ë–æ–ª—å—à–æ–π –ª–æ–≥–æ—Ç–∏–ø (–ø–æ–¥ —à–∞–ø–∫–æ–π)
    bigLogo.style.opacity = String(1 - ratio);
    bigLogo.style.transform = `scale(${1 - 0.1 * ratio})`;

    // –ú–∞–ª—ã–π –ª–æ–≥–æ—Ç–∏–ø (–≤ —à–∞–ø–∫–µ)
    smallLogo.style.opacity = String(ratio);
    smallLogo.style.transform = `scale(${0.9 + 0.1 * ratio})`;
  }

  // rAF-—Ç—Ä–æ—Ç—Ç–ª–∏–Ω–≥ –¥–ª—è –ø–ª–∞–≤–Ω–æ—Å—Ç–∏
  let ticking = false;
  function onScroll() {
    if (ticking) return;
    ticking = true;
    requestAnimationFrame(() => {
      apply();
      ticking = false;
    });
  }

  // –í–ê–ñ–ù–û: —Å—Ä–∞–∑—É –ø—Ä–∏–º–µ–Ω—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ ‚Äî –ø–æ–∫—Ä—ã–≤–∞–µ—Ç reload/HMR –≤–Ω–∏–∑—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  apply();

  // –°–ª—É—à–∞—Ç–µ–ª–∏
  window.addEventListener('scroll', onScroll, { passive: true });
  window.addEventListener('load', apply);
  window.addEventListener('resize', apply);
}