// –ü–ª–∞–≤–Ω—ã–π —Å–∫—Ä–æ–ª–ª –ø–æ —è–∫–æ—Ä—è–º —Å –≥—Ä—É–±–æ–π –∫–æ—Ä—Ä–µ–∫—Ü–∏–µ–π (tweak) –æ—Ç —Ñ–∞–∫—Ç–∏—á–µ—Å–∫–æ–π –≤–∏–¥–∏–º–æ–π –≤—ã—Å–æ—Ç—ã —Ö–µ–¥–µ—Ä–∞
export function initAnchorScroll(options = {}) {
  const {
    headerSelector = ".header",
    extraOffset = 10,   // –Ω–µ–±–æ–ª—å—à–æ–π –∑–∞–ø–∞—Å –ø–æ–¥ —Ö–µ–¥–µ—Ä–æ–º
    tweak = 20          // –°–ö–û–õ–¨–ö–û –ø–∏–∫—Å–µ–ª–µ–π –û–¢–ù–Ø–¢–¨ (–≥—Ä—É–±–∞—è –∫–æ—Ä—Ä–µ–∫—Ü–∏—è)
  } = options;

  const root   = document.documentElement;
  const header = document.querySelector(headerSelector);

  // —Ç–µ–∫—É—â–∞—è –≤–∏–¥–∏–º–∞—è –≤—ã—Å–æ—Ç–∞ —Ö–µ–¥–µ—Ä–∞ (–µ—Å–ª–∏ –æ–Ω —á–∞—Å—Ç–∏—á–Ω–æ —Å–∫—Ä—ã—Ç)
  const getVisibleHeaderHeight = () => {
    if (!header) return 0;
    const r = header.getBoundingClientRect();
    const top = Math.max(r.top, 0);
    const bottom = Math.min(r.bottom, window.innerHeight);
    return Math.max(0, bottom - top);
  };

  // —ç—Ñ—Ñ–µ–∫—Ç–∏–≤–Ω—ã–π –æ—Ñ—Ñ—Å–µ—Ç —Å —É—á—ë—Ç–æ–º –≥—Ä—É–±–æ–π –∫–æ—Ä—Ä–µ–∫—Ü–∏–∏
  const computeOffset = () => {
    const raw = getVisibleHeaderHeight() + extraOffset;
    const effective = Math.max(0, raw - tweak); // ‚Üê "–º–∏–Ω—É—Å—É–µ–º" tweak
    root.style.setProperty("--header-offset", `${effective}px`);
    return effective;
  };

  const scrollToHash = (hash) => {
    if (!hash || hash === "#" || hash === "#!") return;

    if (hash === "#top") {
      window.scrollTo({ top: 0, behavior: "smooth" });
      return;
    }

    const target = document.querySelector(hash);
    if (!target) return;

    const offset = computeOffset();

    // –ø–æ–∑–∏—Ü–∏—è —Å —É—á—ë—Ç–æ–º –æ—Ç–∫–æ—Ä—Ä–µ–∫—Ç–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ –æ—Ñ—Ñ—Å–µ—Ç–∞
    const y = Math.max(0, target.getBoundingClientRect().top + window.scrollY - offset);
    window.scrollTo({ top: y, behavior: "smooth" });

    // –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å
    target.setAttribute("tabindex", "-1");
    target.focus({ preventScroll: true });
  };

  const onAnchorClick = (e) => {
    const a = e.currentTarget;
    const href = a.getAttribute("href") || "";
    if (!href.startsWith("#")) return;

    // –µ—Å–ª–∏ —Ü–µ–ª–∏ –Ω–µ—Ç (–∫—Ä–æ–º–µ #top) ‚Äî –Ω–µ –ø–µ—Ä–µ—Ö–≤–∞—Ç—ã–≤–∞–µ–º
    if (!document.querySelector(href) && href !== "#top") return;

    e.preventDefault();
    scrollToHash(href);
    history.pushState(null, "", href);
  };

  const bindAnchors = () => {
    document.querySelectorAll('a[href^="#"]').forEach((a) => {
      a.removeEventListener("click", onAnchorClick);
      a.addEventListener("click", onAnchorClick);
    });
  };

  // init
  bindAnchors();
  window.addEventListener("resize", computeOffset);

  if (location.hash) {
    setTimeout(() => scrollToHash(location.hash), 0);
  }

  window.addEventListener("hashchange", () => scrollToHash(location.hash));
}
